.reasons {
  display: flex;
  grid-template: 2rem;
  padding: 0 2rem;
}

.left-r {
  flex: 1 1;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  grid-auto-rows: 1fr;
}

.left-r > img {
  object-fit: cover;
}

.left-r > :nth-child(1) {
  height: 28rem;
  width: 12rem;
  grid-row: 1/3;
}
.left-r > :nth-child(2) {
  height: 16rem;
  width: auto;
  grid-column: 2/4;
}
.left-r > :nth-child(3) {
  width: 14rem;
  grid-column: 2/3;
  grid-row: 2;
}
.left-r > :nth-child(4) {
  width: 10rem;
  grid-column: 3/4;
  grid-row: 2;
  height: 11.2rem;
}

.right-r {
  flex: 1 1;
  text-transform: uppercase;
  gap: 1rem;
  display: flex;
  flex-direction: column;
}

.right-r > span {
  font-weight: bold;
  color: var(--orange);
}

.right-r > div {
  color: white;
  font-size: 3rem;
  font-weight: bold;
}
.details-r {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.details-r > div {
  display: flex;
  font-size: 1rem;
  gap: 1rem;
}

.details-r > div > img {
  width: 2rem;
  height: 2rem;
}

.partners {
  display: flex;
  gap: 1rem;
}

.partners > img {
  width: 2rem;
  height: 2rem;
}

@media screen and (max-width: 768px) {
  .reasons {
    flex-direction: column;
  }
  .left-r {
    overflow: hidden;
    grid-auto-rows: auto;
  }

  .left-r > :nth-child(1) {
    height: 17rem;
    width: 7rem;
  }
  .left-r > :nth-child(2) {
    height: 8rem;
  }
  .left-r > :nth-child(3) {
    height: 8rem;
    width: 5rem;
  }
  .left-r > :nth-child(4) {
    height: 8rem;
    width: 5rem;
  }
}
