.footer-container {
  position: relative;
}
.footer-container > hr {
  border: 1px solid var(--lightgray);
}
.footer {
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  gap: 4rem;
  align-items: center;
  justify-content: center;
  height: 20rem;
}
.social-links {
  display: flex;
  gap: 4rem;
}

.social-links > img {
  width: 2rem;
  height: 2rem;
  cursor: pointer;
}

.logo-f > img {
  width: 10rem;
}

.blurf1 {
  bottom: 0;
  left: 15%;
  height: 12rem;
  width: 26rem;
  filter: blur(200px);
  background: orangered;
}
.blurf2 {
  bottom: 0;
  right: 15%;
  height: 12rem;
  width: 26rem;
  filter: blur(200px);
  background: red;
}
